<template>
  <div>
    <div class="row">
      <div class="col-12 bg-black h-10 shadow-lg rounded-lg">
        <button
          type="button"
          @click="$router.push('/')"
          class="btn btn-sm btn-danger absolute"
          style="top: 5px; left: 40px"
        >
          <span class="typcn typcn-arrow-left"></span>
        </button>
        <p class="text-center p-2 font-bold text-white">UMUM</p>
      </div>
      <div class="col-12 p-3">
        <p class="text-center text-2xl font-bold">INPUT UMUM</p>
        <div class="p-3">
            <div class="sm-form">
                <p class="p-2 font-bold">INFO : </p>
            <textarea
                type="text"
                id="info"
                name="info"
                rows="2"
                placeholder="info..."
                class="form-control md-textarea"
                v-model="vdata.info"
            ></textarea>
            </div>
            <div class="sm-form">
                <p class="p-2 font-bold">KET : </p>
            <textarea
                type="text"
                id="ket"
                name="ket"
                rows="2"
                placeholder="ket..."
                class="form-control md-textarea"
                v-model="vdata.ket"
            ></textarea>
            </div>
            <hr class="style13" />
            <button type="submit" @click="add()" class="btn btn-sm btn-primary">Tambah +</button>
            <button type="submit" @click="update(vdata.id)" class="btn btn-sm btn-success">Update <span class="typcn typcn-pencil"></span></button>
            <button type="submit" @click="hapus(vdata.id)" class="btn btn-sm btn-danger">Delete X</button>
        </div>
      </div>
      <div class="offset-1 col-11">
        <div
          class="table-responsive-sm font-times p-3"
          style="box-shadow: 1px 4px 6px black; overflow: scroll"
          v-dragscroll
        >
          <table
            class="table-print table-sm table-bordered table-striped table-hover table-print border-black"
            style="
              overflow-x: scroll;
              overflow-y: hidden;
              height: 80px;
              white-space: nowrap;
            "
          >
            <thead class="mdb-color bg-black">
              <tr class="text-white">
                <th style="color:white;" class="">Info</th>
                <th style="color:white;">Ket</th>
              </tr>
            </thead>
            <tbody>
              <tr @click="ambil(item)" v-for="(item, index) in datanya" :key="index+'datanya'">
                <td>{{item.info}}</td>
                <td>{{item.ket}}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import firebase from "firebase";
let db = firebase.firestore();
export default {
  data() {
    return {
      vdata: {},
      datanya:[]
    };
  },
  methods: {
    ambil(item){
        this.vdata = JSON.parse(JSON.stringify(item))
        console.log(item)
        this.$forceUpdate()
    },
    add() {
        db.collection('tv-umum').doc().set(this.vdata,{merge:true}).then(res=>{
            console.log("BERHASIL INSERT")
        }).catch(err=>{
            console.log("err",err)
        })
    },
    update(id) {
        if(id){
            if(confirm("Yakin Edit ?")){
                db.collection('tv-umum').doc(id).set(this.vdata,{merge:true}).then(res=>{
                    console.log("UPDATE BERHASIL")
                }).catch(err=>{
                    console.log("err",err)
                })
            }
        }else{
            alert('KLIK SALAH SATU DATA!')
        }
        
    },
    hapus(id) {
        if(id){
            if(confirm("YAKIN DIHAPUS ?")){
                db.collection('tv-umum').doc(id).delete().then(res=>{
                    console.log("DELETE BERHASIL")
                }).catch(err=>{
                    console.log("err",err)
                })
            }
        }else{
            alert('KLIK SALAH SATU DATA!')
        }
        
    },
    getData(){
        db.collection('tv-umum').onSnapshot(res=>{
            let data = res.docs.map(e=>{
                return {
                    id:e.id,
                    ...e.data()
                }
                })
            console.log(data)
            this.datanya=data
        })
    },
  },
  mounted() {
      this.getData()
  },
};
</script>